/* @media */
/* retina */
html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  font-family: "Manrope", "Arial", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.5;
  color: #131313;
  margin: 0;
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

picture {
  display: block; }

/* Manrope Regular */
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/manrope_400.woff2") format("woff2"), url("../fonts/manrope_400.woff") format("woff"); }

/* Manrope SemiBold */
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/manrope_600.woff2") format("woff2"), url("../fonts/manrope_600.woff") format("woff"); }

/* Manrope ExtraBold */
@font-face {
  font-family: "Manrope";
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: url("../fonts/manrope_800.woff2") format("woff2"), url("../fonts/manrope_800.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  padding: 0 100px;
  max-width: 1600px;
  margin: 0 auto; }

.header {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-column-gap: 50px;
  width: 100%;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px; }

.navigation {
  justify-self: end; }

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

.navigation__item:not(:last-child) {
  margin-right: 35px; }

.navigation__link {
  color: #000;
  font-weight: 600;
  font-size: 16px; }

.article {
  display: grid;
  grid-template-columns: 536px 536px 248px;
  grid-template-rows: repeat(6, auto);
  grid-template-areas: "ah aib aib" "st aib aib" "nd rd v1" "h2 h1 v2" "th fth v3" "l l l";
  grid-gap: 50px 40px; }

.article__header {
  grid-area: ah; }

.article__title {
  margin: 0 0 30px 0;
  font-size: 48px;
  line-height: 1.3; }

.article__text {
  margin: 0; }
  .article__text--first {
    grid-area: st; }
  .article__text--second {
    grid-area: nd; }
  .article__text--third {
    grid-area: rd; }
  .article__text--fourth {
    grid-area: th; }
  .article__text--fifth {
    grid-area: fth; }

.article__intro {
  font-size: 22px;
  font-weight: 600; }

.article__image--big {
  grid-area: aib; }

.article__image--large {
  grid-area: l; }

.article__image-v--first {
  grid-area: v1; }

.article__image-v--second {
  grid-area: v2; }

.article__image-v--third {
  grid-area: v3; }

.article__image-h--first {
  grid-area: h1; }

.article__image-h--second {
  grid-area: h2; }

.footer {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr auto;
  grid-column-gap: 50px;
  align-items: center;
  padding-top: 50px;
  padding-bottom: 50px; }

.footer__copyright {
  margin: 0;
  font-size: 16px; }
